<template>
  <div id="lesson-one">
    <head-top :title="title" />
    <div class="first">
      <div class="like">
        <img :src="general.src" alt="" />
        <div>
          <van-icon name="like-o" />
          <span>{{ general.like }}</span>
        </div>
      </div>
      <div class="text-title">
        <p>{{ general.name }}</p>
        <p>创建者：{{ general.createPerson }}</p>
        <p>时间：{{ general.date }}</p>
        <p :class="{ info: isactive }">简介：{{ general.intro }}</p>
        <p style="color:#00CBAB" @click="Expand">{{ this.expandText }}</p>
      </div>
    </div>
    <div class="content">
      <div name="nav">
        <div>课单内容</div>
        <div>
          <van-button
            icon="plus"
            plain
            type="primary"
            size="small"
            class="c-btn"
            color="#00CBAB"
            >关注</van-button
          >
        </div>
      </div>
      <div v-for="(item, i) in content" :key="i" style="display:flex">
        <div class="left">
          <img :src="item.src" alt="" />
          <div>
            <span>共{{ item.blues }}集</span>
          </div>
        </div>
        <div class="right">
          <p>{{ item.name }}</p>
          <p>{{ item.number }}万人观看</p>
        </div>
      </div>
    </div>
    <div class="bottom-lesson">
      <van-button type="info" color="#00CBAB">加入计划</van-button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      title: "带你走向向往的课堂",
      isactive: true,
      expandText: "详情",
      general: {
        src: "https://img.yzcdn.cn/vant/cat.jpeg",
        name: "带你走向向往的课堂",
        like: 2334,
        createPerson: "方礼红",
        date: "2020-03-03 10:25:36",
        intro:
          "阎清宗打出阎罗混世印，满天祖文浮现出来，犹如千万星辰悬浮在虚无空间，轰击在冥祖身上，将其打得四分五裂"
      },
      content: [
        {
          src: "https://img.yzcdn.cn/vant/cat.jpeg",
          number: 32,
          name: "自然传奇，领略大自然的独特魅力领略",
          blues: 2 // 集数
        },
        {
          src: "https://img.yzcdn.cn/vant/cat.jpeg",
          number: 32,
          name: "自然传奇，领略大自然的独特魅力领略",
          blues: 2 // 集数
        }
      ]
    };
  },
  methods: {
    Expand() {
      this.isactive = !this.isactive;
      if (this.isactive) this.expandText = "详情";
      else this.expandText = "概略";
    }
  }
};
</script>
<style lang="less">
#lesson-one {
  #headTop .header_title {
    font-size: 18px;
  }
  .num {
    position: absolute;
    background: rgba(0, 0, 0, 1);
    opacity: 0.4;
    padding: 5px 6px;
    border-radius: 10px;
    color: #fff;
    right: 5px;
    bottom: 8px;
  }
  .first {
    display: flex;
    background: #fff;
    padding: 15px;
    margin-bottom: 16px;
    .like {
      position: relative;
      height: 105px;
    }
    .like > div {
      .num;
    }
    .van-icon {
      top: 1px;
    }
    img {
      width: 105px;
      height: 105px;
      border-radius: 5px;
    }
    .text-title p {
      color: #999999;
      font-size: 12px;
      text-align: left;
      margin-left: 12px;
      margin-bottom: 6px;
    }
    .text-title p:first-child {
      color: #333;
      font-size: 18px;
    }
    .text-title p:last-child {
      text-align: right;
      margin-top: -5px;
    }
    .text-title p:nth-child(4) {
      line-height: 20px;
    }
    .info {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
    }
  }
  .content {
    background: #fff;
    padding: 15px;
    height: 500px;
    .c-btn {
      float: right;
    }
    div[name="nav"] {
      height: 50px;
    }
    div[name="nav"] div:first-child {
      font-size: 18px;
      float: left;
    }
    .left {
      position: relative;
      margin-bottom: 25px;
    }
    .left > div {
      .num;
    }
    .left img {
      width: 115px;
      height: 64px;
      border-radius: 5px;
    }
    .right p {
      font-size: 14px;
      text-align: left;
      margin-left: 12px;
      margin-bottom: 12px;
    }
    .right p:last-child {
      font-size: 10px;
      color: #999999;
    }
  }
  .bottom-lesson {
    position: fixed;
    bottom: 0px;
    width: 100vw;
    button {
      width: 100%;
    }
  }
}
</style>



